import React, {useRef, useEffect, useState, createElement} from 'react';
import io from 'socket.io-client';
import Peer from 'peerjs';

const socket = io.connect('https://rocky-reef-68087.herokuapp.com');
// const socket = io.connect('http://localhost:8080');

const callOptions={'iceServers': [
	{url: 'stun:95.xxx.xx.x9:3479',		
	username: "user",
	credential: "xxxxxxxxxx"},
	{ url: "turn:95.xxx.xx.x9:3478",		
	username: "user",
	credential: "xxxxxxxx"}]
}; 

const PeerConnection = window.mozRTCPeerConnection || window.webkitRTCPeerConnection;
const SessionDescription = window.mozRTCSessionDescription || window.RTCSessionDescription;
const IceCandidate = window.mozRTCIceCandidate || window.RTCIceCandidate;
navigator.getUserMedia = ( navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia);
let newPeers = {};

let peer = new Peer({config: callOptions})
let peercall;
const myId = {};

export default function Chat(props) {
	const [catchIt, setCatchIt] = useState(false);
	const videos = useRef(null);
	const video = useRef(null);
	const room = props.user.room;
	const name = props.user.name;
	let peers = [];

	useEffect(()=>{

		socket.emit('join', room, name);
		peer = new Peer({config: callOptions})
		
		peer.on('open', function(peerID) {
			myId[name] = peerID;
			socket.send(myId, name);
		});

		peer.on('close', (peerID)=>{
			socket.emit('close', myId);
		})

		socket.on('message', async (data)=>{
			console.log(data);
			peers = data;
			delete peers[name];
		});

		peer.on('call', function(call) {
			peercall=call;
			setCatchIt(true);
		});

}, []);

	function call(){
		navigator.getUserMedia({video: true, audio: true}, getStream, error);
	}

	function answer() {
		console.log('test');
		navigator.mediaDevices.getUserMedia ({ audio: true, video: true }).then(function(mediaStream) {    				  
		peercall.answer(mediaStream);
		video.current.srcObject = mediaStream;
		setTimeout(function() {
			const video = document.createElement('video');
			video.srcObject = mediaStream;
			video.play();
			videos.current.append(video);
		},1500);			  				  
						
	}).catch(function(err) { console.log(err.name + ": " + err.message); });
	}

	function getStream(stream){
		for(let singlePeer in peers){
			if(singlePeer !== name){
				console.log(0);
				const peerCall = peer.call(peers[singlePeer] ,stream);
				peerCall.on('stream', function (stream) {
					const video = document.createElement('video');
					video.srcObject = stream;
					video.play();
					videos.current.append(video);
				});	
			}
		}
		// video.current.srcObject = stream;
	}

	function error(e){
		console.log(e);
	}

	return (
		<div className="App">
			<div ref={videos}></div>
			<video ref={video} autoPlay></video>
			<button onClick={call}>Call</button>
			{catchIt && <button onClick={answer}>Submit</button>}
			<button onClick={()=>1}>End Call</button>
		</div>
	);
}